.timer {
  background: rgba(254, 208, 61, 0.08);
  padding: 70px 0;
  &__wrap {
    display: flex;
  }
  &__text {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
    color: #E44E2B;
    margin-top: 42px;
    span {
      font-weight: 800;
      font-size: 55px;
      line-height: 69px;
      color: rgba(81, 3, 180, 0.74);
    }
  }
  &__block {
    display: flex;
    margin-top: 45px;
  }
  &__number {
    display: flex;
    align-items: center;
    gap: 10px;
    span {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 86px;
      height: 86px;
      border-radius: 20px;
      font-family: var(--font-family-source);
      font-weight: 900;
      font-size: 55px;
      line-height: 69px;
      text-align: center;
      color: #FFFFFF;
    }
    &_purple {
      span {
        background: linear-gradient(154.49deg, #5103B4 5.35%, #9C1FFF 83.85%);
        backdrop-filter: blur(2px);
      }
    }
    &_orange {
      span {
        background: linear-gradient(154.49deg, #F8B505 5.35%, #FED03D 83.85%);
        backdrop-filter: blur(2px);
      }
    }
  }
  &__circle {
    width: 40px;
    position: relative;
    &::before {
      content: "";
      background: rgba(81, 3, 180, 0.74);
      height: 10px;
      width: 10px;
      border-radius: 50%;
      position: absolute;
      top: 30px;
      left: 15px;
    }
    &::after {
      content: "";
      background: rgba(81, 3, 180, 0.74);
      height: 10px;
      width: 10px;
      border-radius: 50%;
      position: absolute;
      bottom: 30px;
      left: 15px;
    }
  }
}