@media (max-width: 1200px) {
  .container {
    max-width: 1140px;
  }

  #waiting-modal .modal__content {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 3000;
    position: relative;
    padding: 20px 95px 20px;
  }

  #waiting-modal .title_timer {
    font-size: 20px;
  }

  #waiting-modal .timer__text {
    margin-top: 10px;
  }

  #leave-modal .modal__form .grid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  #leave-modal .modal__content {
    padding: 30px;
  }

  .modal__info {
    gap: 10px;
  }

  .modal-large {
    max-width: 80%;
  }

  .modal__form {
    align-items: center !important;
  }

  .author-funnel {
    &__wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: flex-start;
      gap: 15px;
      background: none;
      height: auto;
    }
    &__item {
      position: static;
    }
  }

  .title__header {
    max-width: 940px;
  }

  .reviews-myforce {
    background: url("../images/reviews.png") -100% center no-repeat;
    &__item {
      max-width: 400px;
    }
  }

  .author {
    background-position: 170% center;
    &__list {
      max-width: 500px;
    }
  }

  .effective-approach {
    &__list {
      flex-wrap: wrap;
      justify-content: center;
      gap: 20px;
    }
  }

  .swiper {
    width: 1000px;
  }

  .join {
    background-position: 412% center;
  }
  .title_join {
    text-align: center;
  }
  .free-test__form {
    align-items: flex-end;
  }

  .free-test__wrap {
    gap: 0;
  }

  .footer__wrap {
    gap: 30px;
  }
  .footer__list {
    gap: 20px;
    li {
      a {
        font-size: 18px;
      }
    }
  }

  .title_timer {
    text-align: center;
  }

  .btn_timer {
    margin: 0 auto;
    margin-top: 50px;
  }

  .timer {
    &__wrap {
      flex-wrap: wrap;
      justify-content: center;
      gap: 40px;
    }
    &__block {
      justify-content: center;
    }
    &__text {
      text-align: center;
    }
  }
  .funnel__wrap {
    gap: 50px;
  }
  .funnle__img {
    svg {
      width: 300px;
      height: 300px;
    }
  }
}

@media (max-width: 1100px) {
  .reviews-myforce {
    background-position: -200% center;
  }
  .join {
    background-position: 1100% center;
  }

  .footer__list li a {
    font-size: 14px;
  }
  .author {
    background-position: 250% center;
    &__list {
      max-width: 500px;
    }
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 960px;
  }

  .reviews-myforce {
    background-position: -1200% center;
    padding-top: 0;
    min-height: auto;
  }

  #leave-modal .modal__title {
    font-size: 30px;
  }

  .author {
    background-position: 500% center;
  }

  .funnel__wrap {
    gap: 0;
  }

  .funnel__img {
    svg {
      width: 200px;
      height: 200px;
    }
  }

  .accordion__wrap {
    justify-content: center;
  }

  .join {
    background-position: 340% center;
  }

  .footer__wrap {
    flex-direction: column;
    align-items: flex-start;
  }

  .swiper {
    width: 700px;
  }

  .swiper-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    left: 75%;
  }

  .title {
    font-size: 40px;
  }

  .title__header {
    max-width: 800px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 720px;
  }
  #waiting-modal .timer__text {
    margin-left: auto;
    margin-right: auto;
    font-size: 20px;
  }

  #waiting-modal .title_timer {
    line-height: 23px;
  }
  .modal__content {
    padding: 20px;
  }

  .modal__close {
    top: 10px;
  }

  .timer__number {
    span {
      width: 50px;
      height: 50px;
      border-radius: 10px;
      font-size: 40px;
    }
  }

  .funnel__img {
    display: none;
  }

  .funnel__img {
    svg {
      width: 200px;
      height: 200px;
    }
  }

  .join {
    &__wrap {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-left: 0;
      max-width: 100%;
    }
  }

  .author {
    background-position: -280% center;
  }

  .title_join {
    text-align: center;
  }
  .title_free-test {
    text-align: center;
  }
  .btn {
    margin: 0 auto;
  }

  .free-test__wrap {
    flex-direction: column;
    gap: 50px;
  }

  .footer__list {
    flex-direction: column;
  }

  .promo__list_item {
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
    margin-bottom: 30px;
  }

  .reviews-myforce {
    min-height: auto;
  }

  .reviews-myforce__item {
    max-width: 100%;
    margin-top: 0;
  }

  .swiper {
    width: 100%;
  }

  .swiper-slide_img {
    display: none;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 540px;
  }

  #waiting-modal .modal__content {
    padding: 20px;
  }

  .modal-large {
    max-width: 100%;
  }

  .funnel__item {
    max-width: 100%;
  }

  .funnel__form {
    justify-content: center;
    align-items: center;
  }

  .funnel__img {
    svg {
      display: none;
    }
  }

  .swiper-slide {
    height: auto;
    &_img {
      display: none;
    }
  }
  .swiper {
    width: 400px;
  }

  .nav {
    display: none;
  }

  .nav__mobile {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 280px;
    background: var(--bg-gradient);
    position: absolute;
    top: 80px;
    left: -100%;
    border-radius: 0 0 10px 10px;
    transition: 0.4s;
    a {
      padding: 20px;
    }
    &__active {
      left: 0;
    }
  }

  .burger {
    position: absolute;
    top: 25px;
    right: 18px;
    width: 40px;
    height: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    span {
      display: block;
      width: 100%;
      height: 2px;
      background-color: #fff;
    }
    &__active {
      transition: 0.4s;
      span {
        transition: 0.4s;
        position: absolute;
        &:nth-child(1) {
          top: 15px;
          left: 0;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          bottom: 13px;
          left: 0;
          transform: rotate(-45deg);
        }
        &:nth-child(3) {
          display: none;
        }
      }
    }
  }

  .promo {
    background: linear-gradient(151.71deg, #e8b40b 12.47%, #f99e4b 58.27%);
    box-shadow: inset 0px 4px 25px rgba(117, 71, 3, 0.19);
  }

  .swiper-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    display: none;
  }
  .funnel__form {
    width: 100%;
    align-items: center;
    justify-content: center;
    label {
      input {
        width: 278px;
        max-width: 100%;
      }
    }
  }

  .swiper-slide h3 {
    padding-left: 10px;
  }

  .swiper-slide h4 {
    padding-left: 10px;
  }

  .swiper-slide ul {
    padding-left: 10px;
  }
}

@media (max-width: 400px) {
  .container {
    max-width: 100%;
    padding: 10px;
  }
  .modal__title {
    font-size: 20px;
    margin-bottom: 10px;
  }
  .swiper {
    width: 300px;
  }

  #leave-modal .textarea {
    width: 290px;
  }

  .title {
    font-size: 30px;
    line-height: 33px;
  }

  .promo {
    padding-top: 90px;
    &__wrap {
      margin-top: 40px;
    }
    &__text {
      font-size: 16px;
      line-height: 30px;
    }
  }

  .timer__block {
    width: 100%;
  }

  .timer__circle {
    width: 20px;
    &::before {
      left: 5px;
      top: 5px;
    }
    &::after {
      left: 5px;
      bottom: 5px;
    }
  }

  .timer__number {
    gap: 5px;
    span {
      width: 35px;
      height: 35px;
      font-size: 20px;
    }
  }

  input {
    width: 290px;
  }

  .timer__form {
    width: 100%;
    align-items: flex-start;
  }

  .timer__form input {
    width: 290px;
    max-width: 100%;
  }

  .btn {
    margin-right: auto;
  }

  .accordion__item {
    padding: 60px 30px 38px 30px;
  }

  .swiper {
    &-slide {
      h3 {
        padding-left: 10px;
      }
      h4 {
        padding-left: 10px;
      }
      ul {
        padding-left: 10px;
      }
    }
  }
}
