.swiper {
  width: 1440px;
  height: 600px;
  display: flex;
  gap: 40px;
  margin-top: 50px;
  &-container {
    height: 550px;
  }
  &-pagination {
    display: flex;
    align-items: center;
    gap: 15px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -81px;

    &-bullet {
      width: 15px;
      height: 15px;
      background: #ffe593;
      box-shadow: 0px 4px 8px rgba(248, 181, 5, 0.29);
      border-radius: 39px;
      &-active {
        background: #fed03d;
        box-shadow: 0px 4px 8px rgba(248, 181, 5, 0.29);
      }
    }
  }

  &-slide {
    position: relative;
    height: 453px;
    padding-top: 50px;
    background: linear-gradient(151.71deg, #fff1be 12.47%, #ffddbd 58.27%);
    box-shadow: inset 0px 4px 25px rgba(117, 71, 3, 0.19);
    border-radius: 50px;
    transition: background 0.4s ease;
    &:hover {
      background: linear-gradient(151.71deg, #e8b40b 12.47%, #f99e4b 58.27%);
    }
    h3 {
      font-weight: 500;
      font-size: 25px;
      line-height: 30px;
      color: #38027d;
      margin-bottom: 25px;
      padding-left: 50px;
    }
    h4 {
      font-weight: 700;
      font-size: 30px;
      line-height: 37px;
      color: #38027d;
      padding-left: 50px;
    }
    ul {
      display: flex;
      flex-direction: column;
      gap: 25px;
      margin-top: 25px;
      padding-left: 50px;
      li {
        display: flex;
        flex-direction: column;
        gap: 15px;
        p {
          font-weight: 500;
          font-size: 17px;
          line-height: 21px;
          color: #170035;
          span {
            font-size: 20px;
            line-height: 24px;
            color: #38027d;
          }
        }
      }
    }
    &_img {
      position: absolute;
      right: 0;
      top: 85px;
    }
  }
}

.slider {
  padding-bottom: 192px;
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 0;
  left: 90%;
}
