.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  padding: 0;
  max-width: 846px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(0.2);
  opacity: 0;
  transition: 0.3s linear;
  border: 0;
  visibility: hidden;
  border-radius: 50px;
  background: var(--bg-gradient);
  z-index: 5000;

  &[open] {
    transform: scale(1);
    opacity: 1;
    visibility: visible;
    transition: 0.6s linear;
  }

  &::backdrop {
    background: rgba(0, 0, 0, 0.5);
    cursor: pointer;
  }

  &__content {
    padding: 90px;
  }

  &__title {
    color: #fff;
    font-size: 40px;
    line-height: 50px;
    font-weight: 800;
    margin-bottom: 44px;
  }

  &__form {
    align-items: center;
  }

  &__label {
    font-weight: 500;
    align-items: flex-start;
    input,
    .textarea {
      background: #fff;
    }
  }

  &__close {
    color: #fff;
    position: absolute;
    width: 30px;
    height: 30px;
    display: block;
    top: 40px;
    right: 40px;
    cursor: pointer;
    z-index: 100;
    pointer-events: fill;
  }

  button {
    margin-top: 35px;
  }

  &-large {
    max-width: 1440px;
    background: #fed03d;
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 45px;
  }
}

#waiting-modal {
  .modal__content {
    display: flex;
    align-items: center;
    gap: 30px;
    padding: 115px 95px 95px;
  }

  .title_timer {
    margin-top: 0;
    text-align: left;
    letter-spacing: 0;
  }

  form {
    align-items: flex-start;
  }
}

#leave-modal {
  .modal__title,
  .modal__subtitle {
    color: #38027d;
    margin-bottom: 40px;
  }

  .modal__title {
    max-width: 785px;
    font-size: 55px;
    font-weight: 700;
    line-height: 1.2;
  }

  .modal__subtitle {
    font-size: 25px;
    font-weight: 600;
  }

  .grid {
    display: grid;
    gap: 10px 20px;
    grid-template: repeat(2, 1fr) / repeat(2s, 1fr);
    label:nth-child(3) {
      grid-row: 2 / 3;
    }
    label:nth-child(2) {
      grid-row: 1 / 3;
      grid-column: 3 / 5;
    }
  }

  .textarea {
    height: 100%;
    padding: 10px;
    resize: none;
  }
}
