.free-test {
  padding: 60px 0;
  background: rgba(254, 208, 61, 0.08);
  &__wrap {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 203px;
  }
  &__form {
    display: flex;
    flex-direction: column;
    gap: 15px;

    label {
      display: flex;
      flex-direction: column;
      gap: 5px;
    }
  }
}
