.author {
  background-color: #F0F0F0;
  padding: 143px 0;
  background-image: url("../images/author.png") ;
  background-position: 115% center;
  background-repeat: no-repeat;
  &__subtitle {
    font-weight: 800;
    font-size: 45px;
    line-height: 56px;
    letter-spacing: 0.035em;
    color: #E44E2B;
    margin-top: 55px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 30px;
    li {
      font-size: 17px;
      color: #170035;
    }
  }
}