.btn {
  background: #9714ff;
  border-radius: 10px;
  width: 239px;
  height: 62px;
  transition: 0.4s;
  font-weight: 700;
  font-size: 20px;
  text-align: center;
  color: #ffffff;
  &:hover {
    box-shadow: 0px 2px 50px rgba(189, 48, 255, 0.1), 0px 6px 0px #5c00d1,
      inset 0px 0px 14px rgba(255, 255, 255, 0.3);
  }
  &__promo {
    width: 300px;
    height: 80px;
    font-weight: 700;
    line-height: 25px;
    margin-top: 50px;
  }
  &_funnel {
    margin-top: 15px;
  }
  &_timer {
    margin-top: 50px;
  }
}
