// Сбрасываем стили по тегам
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: #fff;
}

img {
  width: 100%;
  height: auto;
}

html {
  height: 100%;
}

.slick-arrow {
  display: none !important;
}

input,
textarea {
  max-width: 100%;
  outline: none;
  background: rgba(254, 208, 61, 0.3);
  border: 1px solid #f8b505;
  border-radius: 14px;
  width: 349px;
  height: 49px;
  padding: 0 10px;
}

body {
  margin: 0;
  min-height: 100%;
  font-family: var(--font-family-gilroy);
  line-height: 20px;
  font-weight: 400;
  overflow-x: hidden;
  color: var(--color-white);
}
.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}
