.header {
  padding: 30px 0;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  border-radius: 0 0 5px 5px;
  z-index: 10000;
  &__wrap {
    display: flex;
    align-items: center;
    gap: 70px;
  }
  &__active {
    background: var(--bg-gradient);
    box-shadow: inset 0px 4px 25px rgba(117, 71, 3, 0.19);
    animation-name: fade;
    animation-duration: 1.5s;
  }
}

.nav {
  display: flex;
  align-items: center;
  gap: 40px;
  a {
    font-weight: 600;
    font-size: 17px;
    color: #ffffff;
    transition: 0.5s ease;

    &:hover {
      color: #e44e2b;
    }
  }
}

@keyframes fade {
  from {
    opacity: 0.1;
  }

  to {
    opacity: 1;
  }
}
