.accordion {
  position: relative;
  display: flex;
  align-items: center;
  text-align: left;
  margin-bottom: 30px;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
  color: #170035;
  cursor: pointer;
  &.active {
    div {
      transform: rotate(180deg);
    }

    & + .panel {
      display: flex;
    }
  }
  div {
    display: block;
    width: 30px;
    height: 30px;
  }
  &__wrap {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 60px;
    gap: 30px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    width: 223px;
    padding: 60px 50px 38px 50px;
    border-radius: 50px;
    background: rgba(105, 105, 105, 0.1);
    transition: 0.4s;
    overflow: hidden;
    &:hover {
      background: linear-gradient(151.71deg, #e8b40b 12.47%, #f99e4b 58.27%);
      box-shadow: inset 0px 4px 25px rgba(117, 71, 3, 0.19);
    }
  }

  &__title {
    font-weight: 700;
    font-size: 25px;
    line-height: 31px;
    color: #38027d;
    text-align: center;
    margin-bottom: 30px;
    &_mt {
      margin-top: -31px;
    }
    &_wc {
      white-space: nowrap;
    }
  }
}

.panel {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin-bottom: 30px;

  div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
    p {
      font-weight: 400;
      font-size: 17px;
      line-height: 20px;
      text-align: center;
      color: #170035;
    }
    span.included {
      font-weight: 600;
      font-size: 17px;
      line-height: 21px;
      color: #bd30ff;
    }
  }
}

.price {
  font-weight: 600;
  font-size: 40px;
  line-height: 49px;
  color: #e44e2b;
  margin-top: 25px;
  margin-bottom: 25px;
  text-align: center;
  white-space: nowrap;
  &__text {
    font-weight: 600;
    font-size: 25px;
    line-height: 31px;
    text-align: center;
    color: #38027d;
  }
}
