.promo {
  min-height: 905px;
  padding-top: 150px;
  position: relative;
  background-image: url("../images/promo-bg.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-color: #f0f0f0;
  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 30px;
    li {
      font-weight: 600;
      font-size: 17px;
      line-height: 21px;
      color: #ffffff;
    }
    &_item {
      margin-top: 65px;
      display: flex;
      gap: 50px;
      align-items: center;
      li {
        display: flex;
        align-items: center;
        gap: 15px;
        p {
          max-width: 267px;
          font-size: 17px;
          color: #170035;
        }
        p.mw-160 {
          max-width: 160px;
        }
      }
    }
  }
  &__wrap {
    margin-top: 231px;
  }
  &__text {
    max-width: 577px;
    font-weight: 800;
    font-size: 35px;
    line-height: 44px;
    letter-spacing: 0.035em;

    color: #e44e2b;
  }
}
