.partners {
  padding-top: 70px;
  padding-bottom: 131px;
  background-color: #f0f0f0;
  &__subtitle {
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
    color: #e44e2b;
    margin-top: 30px;
  }
  &__text {
    display: flex;
    flex-direction: column;
    gap: 20px;
    max-width: 1013px;
    margin-top: 30px;
    p {
      display: block;
      font-weight: 400;
      font-size: 17px;
      line-height: 20px;
      color: #170035;
    }
    .partners__bold {
      font-weight: 600;
    }
  }
  &__footer {
    margin: 0 auto;
    margin-top: 100px;
    max-width: 500px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    &_title {
      font-weight: 600;
      font-size: 35px;
      line-height: 43px;
      text-align: center;
      color: #5103b4;
    }
  }
}
