.author-funnel {
  padding-top: 120px;
  padding-bottom: 89px;
  &__text {
    font-size: 17px;
    line-height: 20px;
    text-align: center;
    color: #170035;
    max-width: 667px;
    margin: 0 auto;
    margin-top: 30px;
  }
  &__wrap {
    margin-top: 90px;
    position: relative;
    height: 180px;
    background: url("../images/author__block.png") center center no-repeat;
  }
  &__item {
    position: absolute;
    width: 241px;
    div {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 15px;
      p {
        font-size: 17px;
        text-align: center;
        color: #170035;
      }
      div.circle {
        width: 29px;
        height: 29px;
        border-radius: 50%;
        background: #fed03d;
        box-shadow: 0px 4px 8px rgba(248, 181, 5, 0.29);
      }
    }
  }
  &__item:nth-child(1) {
    left: -10px;
    top: -12px;
  }
  &__item:nth-child(2) {
    left: 295px;
    bottom: -67px;
  }
  &__item:nth-child(3) {
    right: 652px;
    top: -11px;
  }
  &__item:nth-child(4) {
    right: 400px;
    top: 163px;
  }
  &__item:nth-child(5) {
    right: 164px;
    top: -11px;
  }
  &__item:nth-child(6) {
    right: -3px;
    bottom: -66px;
  }
}
