.footer {
  padding: 45px 0;
  background: var(--bg-gradient);
  box-shadow: inset 0px 4px 25px rgba(117, 71, 3, 0.19);
  &__wrap {
    display: flex;
    align-items: center;
    gap: 55px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    gap: 15px;
    max-width: 250px;
    p {
      font-size: 11px;
      color: #ffffff;
      a {
        border-bottom: 1px solid #ffff;
      }
    }
  }
  &__list {
    display: flex;
    align-items: flex-start;
    gap: 75px;
    li {
      display: flex;
      flex-direction: column;
      gap: 5px;
      line-height: 19px;
      color: #ffffff;
      a {
        font-weight: 700;
        font-size: 25px;
      }
      address {
        max-width: 300px;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        font-style: normal;
      }
      p {
        font-weight: 500;
        font-size: 20px;
      }
    }
  }
}
