.title {
  font-family: var(--font-family-impact);
  font-weight: 400;
  font-size: 55px;
  line-height: 67px;
  color: #5103b4;
  letter-spacing: 0.125em;
  &_free-test {
    font-family: var(--font-family-gilroy);
    font-weight: 600;
    letter-spacing: 0.125em;
    color: #5103b4;
    max-width: 621px;
  }
  &_center {
    text-align: center;
  }
  &__header {
    max-width: 973px;
    font-weight: 400;
    letter-spacing: 0.07em;
    color: #ffffff;
  }
  &_author-funnel {
    text-align: center;
    max-width: 927px;
    margin: 0 auto;
  }
  &_author {
    max-width: 589px;
  }
  &_funnel {
    max-width: 724px;
    font-size: 35px;
    line-height: 43px;
    letter-spacing: 0.07em;
    color: #fff;
  }
  &_effective {
    max-width: 727px;
    font-size: 35px;
    line-height: 43px;
    letter-spacing: 0.125em;
    color: #5103b4;
    margin: 0 auto;
    text-align: center;
  }
  &_join {
    font-size: 25px;
    line-height: 30px;
    margin-bottom: 30px;
  }
  &_timer {
    max-width: 849px;
    font-family: var(--font-family-gilroy);
    font-weight: 700;
    font-size: 35px;
    line-height: 43px;
    color: #38027d;
    margin-top: 20px;
  }
  &_partners {
    max-width: 903px;
    font-weight: 400;
    font-size: 35px;
    line-height: 43px;
  }
  &_slider {
    margin: 0 auto;
    max-width: 722px;
    text-align: center;
  }

  &_permament-partners {
    text-align: center;
  }
}
