.funnel {
  background: url("../images/funnel.svg") center center no-repeat;
  min-height: 814px;
  position: relative;
  z-index: 20;
  margin-bottom: -41px;
  &__wrap {
    display: flex;
    align-items: center;
    gap: 120px;
  }
  &__item {
    margin-top: 192px;
    max-width: 724px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    margin-top: 19px;
    gap: 15px;
    label {
      display: flex;
      flex-direction: column;
      gap: 5px;
      font-weight: 500;
      font-size: 17px;
      color: #ffffff;
      input {
        background: #ffffff;
        opacity: 0.9;
        border: 1px solid rgba(81, 3, 180, 0.74);
        border-radius: 14px;
      }
    }
  }
  &__img {
    margin-top: 239px;
  }
}
